<template>
  <ec-container height="100%" title="需求提报确认" icon="onetomany" color-code="008">
    <ec-container-body>
      <ec-panel :auto="false">
        <ec-panel-toolbar>
          <ec-query-panel @query="$refs.table.pageAction(1)" @reset="onReset">
            <ec-query-item label="采集批次">
              <a-select allow-clear placeholder="请选择" v-model="queryItems.batchName">
                <a-select-option v-for="item in batchList" :key="item.batchName">
                  {{ item.batchName }}
                </a-select-option>
              </a-select>
            </ec-query-item>

            <ec-query-item label="确认状态">
              <a-select allow-clear placeholder="请选择" v-model="queryItems.auditState">
                <a-select-option v-for="item in auditStateList" :key="item.id">
                  {{ item.name }}
                </a-select-option>
              </a-select>
            </ec-query-item>
            <ec-query-item label="需求单位">
              <a-select placeholder="请选择" v-model="queryItems.demandOrgCid">
                <a-select-option v-for="item in industryUnitList" :key="item.demandOrgCode">
                  {{ item.demandOrgName }}
                </a-select-option>
              </a-select>
            </ec-query-item>

            <!-- <ec-query-item label="需求单位">
              <a-cascader
                :options="industryUnitTreeData"
                :fieldNames="fieldNames"
                placeholder="请选择工业单位"
                @change="onSelIndustryUnit"
              />
            </ec-query-item> -->

            <!-- <ec-query-item label="需求单位">
              <org-picker v-model="demandOrgList" placeholder="请选择" />
            </ec-query-item> -->

            <ec-query-item label="供方单位">
              <select-picker
                title="选择供方单位"
                :lazy="false"
                :search="false"
                :fetchData="onFetchBusinessOrgList"
                v-model="queryItems.supplyOrgCode"
              />
            </ec-query-item>
            <!-- <ec-query-item label="供方单位">
              <org-picker v-model="queryItems.supplyOrgCode" placeholder="请选择" />
            </ec-query-item> -->
          </ec-query-panel>
          <ec-divider />
        </ec-panel-toolbar>
        <ec-panel-header>
          <ec-panel-item>
            <ec-panel-item-left>
              <a-button type="primary" @click="onAgree">同意</a-button>
              <a-button @click="onBack">退回</a-button>
            </ec-panel-item-left>
            <ec-panel-item-right>
              <span class="ant-form-text"> 业务年度： {{ queryItems.businessYear }}</span>
              <span class="ant-form-text"> 计量单位： {{ weightUnitName }}</span>
            </ec-panel-item-right>
          </ec-panel-item>
        </ec-panel-header>
        <ec-panel-body>
          <ec-table
            ref="table"
            border
            height="100%"
            :auto="false"
            @page-action="handlePagination"
            :pagination="pagination"
            :data="tableData.rows"
            @checkbox-all="selectAllEvent"
            @checkbox-change="selectRowChange"
            :loading="loading"
            row-select
            show-footer
            :footer-method="footerMethod"
          >
            <ec-table-col type="checkbox" width="50" align="center"></ec-table-col>
            <ec-table-col width="70" title="序号" type="seq" align="center" />
            <ec-table-col field="batchName" title="采集批次" align="left" min-width="150" />
            <!-- <ec-table-col field="demandOrgCid" title="需求单位" align="left" min-width="150">
                  <template #default="{ row }">
                    <div>{{ row.demandOrgCid | getNameBy(industryUnitListObj) }}</div>
                  </template>
                </ec-table-col> -->
            <ec-table-col field="demandOrgName" title="需求单位" align="left" min-width="150" />
            <ec-table-col field="supplyOrgName" title="供方单位" align="left" min-width="150" />
            <ec-table-col field="weight1" :title="weight1Title" align="right" width="150" />
            <ec-table-col field="weight2" :title="weight2Title" align="right" width="150" />
            <ec-table-col field="weight3" :title="weight3Title" align="right" width="150" />
            <ec-table-col field="weight4" :title="weight4Title" align="right" width="150" />

            <ec-table-col field="auditState" title="确认状态" align="left" width="130">
              <template #default="{ row }">
                <div v-if="row.auditState === '5'">
                  <span>总公司已审核</span>
                </div>
                <div v-else-if="row.auditState === '1'">
                  <span>省公司待确认</span>
                </div>
                <div v-else-if="row.auditState === '2'">
                  <span>省公司退回</span>
                </div>
                <div v-else-if="row.auditState === '3'">
                  <span>总公司待审核</span>
                </div>
                <div v-else-if="row.auditState === '4'">
                  <span>总公司退回</span>
                </div>
                <div v-else>
                  <span>未上报</span>
                </div>
              </template>
            </ec-table-col>
            <ec-table-col title="操作" align="center" min-width="130">
              <template #default="{ row }">
                <a-button type="link" @click="onDetail(row)">详细</a-button>
              </template>
            </ec-table-col>
          </ec-table>
        </ec-panel-body>
      </ec-panel>
      <detail ref="Detail"> </detail>
    </ec-container-body>
  </ec-container>
</template>

<script>
import { OrgPicker, SelectPicker } from '@euler-material/ecm-ui';
import Detail from './Detail.vue';
import { treeFormatList } from '@euler-material/ecm-utils';
import _ from 'lodash';

export default {
  name: 'BatchManage',
  components: {
    OrgPicker,
    SelectPicker,
    Detail,
  },
  data() {
    return {
      queryItems: {
        supplyOrgCode: undefined,
        businessYear: '',
        batchName: '',
        auditState: '1',
        orgCode: '',
        demandOrgCid: undefined,
      },
      busiYear: '',
      // isDisable: true,
      weight1Title: '',
      weight2Title: '',
      weight3Title: '',
      weight4Title: '',
      orgCodeList: [],
      supplyOrgList: [],
      demandOrgList: [],
      loading: false,
      yearDisable: true,
      needYearListFormat: {},
      weightColunm: ['weight1', 'weight2', 'weight3', 'weight4'],
      needGatherYear: '',
      defaultBatchName: '',
      defaultNeedGatherYear: '',
      batchList: [],
      mergeDataList: [],
      // 工业单位列表
      industryUnitList: [],
      // 工业单位树数据
      industryUnitTreeData: [],
      // 商业单位
      businessOrgList: [],
      // 组织字段重置
      replaceFields: {
        pkey: 'parentBizCode',
        title: 'orgName',
        key: 'bizCode',
        children: 'children',
      },
      // 树组件字段重置
      fieldNames: { label: 'orgAbbr', value: 'orgUniqueCode', children: 'children' },
      pagination: {
        pageNumber: 1,
        pageSize: 10,
        total: 0,
      },
      bizCode: '',
      auditStateList: [
        { id: '', name: '全部' },
        { id: '1', name: '省公司待确认' },
        { id: '3', name: '总公司待审核' },
        { id: '5', name: '总公司已审核' },
      ],
      oldTableData: [],
      weightUnit: 500000,
      weightUnitName: '万担',
      tableData: {
        // 数据表格
        // totalRows: 0,
        rows: [],
      },
    };
  },

  computed: {
    appConfig() {
      return this.$store.getters.getAppConfig;
    },
  },

  created() {
    const { appCode } = this.$router.history.current.query;
    this.appCode = appCode || 'CM'; // 获取当前登入信息
  },
  methods: {
    // 查询商业单位
    async handleQueryBusinessOrgList() {
      const params = {
        busiYear: this.busiYear,
        orgTypeEk: 'TOBACCO',
        orgLevelEk: '6',
        parentBizCode: this.bizCode,
      };
      const res = await this.$apis.common.index.queryTreeByCode(params);
      this.businessOrgList = res.rows || [];
    },
    // 更新商业单位
    onFetchBusinessOrgList() {
      return new Promise((resolve) => {
        const data = treeFormatList(this.businessOrgList, this.replaceFields);
        resolve(data);
      });
    },

    async handlePagination(pageNumber, pageSize) {
      this.pagination.pageNumber = pageNumber;
      this.pagination.pageSize = pageSize;
      await this.handleGetYear();
      // this.onQuery();
    },

    // 获取年度
    async handleGetYear() {
      if (this.queryItems.businessYear === '') {
        if (!this.orgCode) {
          const orgObj = await this.$apis.bim.curOrg.get({});
          this.bizCode = orgObj?.bizCode;
          this.orgCode = orgObj?.orgUniqueCode; // todo 写死数据
        }
        const data = {
          bizCode: this.appConfig.bizCode, // todo 写死的数据
          appCode: this.appCode,
          yearTypeCode: 'BIZ_YEAR',
        };
        const businessYearObj = await this.$apis.bpm.executionEnter.getBuYear(data);
        this.busiYear = businessYearObj?.yearValue;
        const businessYear = businessYearObj?.yearValue || new Date().getFullYear();
        this.queryItems.businessYear = businessYear;
        console.log(`---------${businessYear}`);
        const needYearListFormat = {};
        this.weight1Title = `${businessYear + 1}年需求`;
        needYearListFormat[businessYear + 1] = 'weight1';
        this.weight2Title = `${businessYear + 2}年需求`;
        needYearListFormat[businessYear + 2] = 'weight2';
        this.weight3Title = `${businessYear + 3}年需求`;
        needYearListFormat[businessYear + 3] = 'weight3';
        this.weight4Title = `${businessYear + 4}年需求`;
        needYearListFormat[businessYear + 4] = 'weight4';
        this.needYearListFormat = needYearListFormat;
        // 获取批次列表，并设置批次的默认值
        const batchParam = {
          busiYear: this.queryItems.businessYear,
        };
        const respone = await this.$apis.apm.demandManage.queryBatch(batchParam);
        const rows = respone?.rows || [];
        this.batchList = rows;
        const defaultBatchName = rows && rows.length > 0 ? rows[0].batchName : '';
        this.queryItems.batchName = defaultBatchName;
        this.defaultBatchName = defaultBatchName;

        const params = {
          busiYear: businessYear,
        };

        const industryObj = await this.$apis.apm.demandQuery.queryIndustry(params);
        this.industryUnitList = industryObj.rows || [];
        console.log(industryObj);
      }

      // 根据批次获取批次的采集年度，并设置动态的年度列
      await this.handleNeedYearColumn();
      if (!this.businessOrgList?.length) {
        await this.handleQueryBusinessOrgList();
      }
      await this.onQuery();
    },

    // 将列转为树结构
    handleListToTree(data) {
      // * 先生成parent建立父子关系
      const obj = {};
      data.forEach((item) => {
        obj[item.orgUniqueCode] = item;
      });
      const treeData = [];
      data.forEach((item) => {
        const parent = obj[item.parentOrgUniqueCode];
        if (parent) {
          // * 当前项有父节点
          parent.children = parent.children || [];
          parent.children.push(item);
        } else {
          // * 当前项没有父节点 -> 顶层
          treeData.push(item);
        }
      });

      // 因为商业单位会返回国家局,所以将第一级去除
      if (treeData.length === 1) {
        return treeData[0].children;
      } else {
        return treeData;
      }
    },

    // 根据批次获取批次的采集年度，并设置动态的年度列
    async handleNeedYearColumn() {
      const that = this;
      this.weightColunm.forEach((item) =>
        this.$refs.table.hideColumn(this.$refs.table.getColumnByField(item)),
      );
      const { batchName } = this.queryItems;
      const data = {
        batchName,
        busiYear: this.queryItems.businessYear,
      };
      const respone = await this.$apis.apm.demandManage.queryNeedGatherYear(data);
      const needGatherYear = respone?.needGatherYear || '';
      if (that.defaultNeedGatherYear === '') {
        that.defaultNeedGatherYear = needGatherYear;
      }
      console.log(`采集年度：${respone?.needGatherYear}`);
      this.needGatherYear = needGatherYear;
      const needGatherYearArr = needGatherYear.split(',');
      needGatherYearArr.forEach((item) => {
        const needYearListFormat = this.needYearListFormat || {};
        const visibleWeightColumn = needYearListFormat[item];
        this.$refs.table.showColumn(this.$refs.table.getColumnByField(visibleWeightColumn));
      });

      // 写死测试
      // const needGatherYear = respone?.needGatherYear || '';
    },

    // 数据初始化
    async onQuery() {
      // const { supplyOrgList } = this;
      // const supplyOrgCode = supplyOrgList.length > 0 ? supplyOrgList[0] : '';
      const selectDemandOrg = this.industryUnitList.find(
        (item) => item.demandOrgCode === this.queryItems.demandOrgCid,
      );
      const data = {
        pageNo: this.pagination.pageNumber,
        pageSize: this.pagination.pageSize,
        busiYear: this.queryItems.businessYear,
        supplyOrgCode: this.queryItems.supplyOrgCode,
        demandOrgCode: selectDemandOrg?.demandOrgCode || '',
        batchName: this.queryItems.batchName,
        auditState: this.queryItems.auditState,
        pageStatus: 'confirm',
      };
      console.log(`-----------参数：${JSON.stringify(data)}`);
      this.loading = true;
      this.$apis.apm.demandConfirm
        .query(data)
        .then((res) => {
          this.loading = false;
          const rows = res?.rows || [];
          this.pagination.total = res.totalRows;
          const oldTableData = _.cloneDeep(rows);
          this.oldTableData = oldTableData;

          // 对返回的数据处理，转成对应的单位数据

          const tableRows = this.handleMergeDetData(rows);
          this.handleUnitParse(tableRows);
        })
        .catch((err) => {
          this.loading = false;
          console.log(err);
        });
    },

    // 处理获取到数据，将细表的数据按年度合并，并根据动态显示S
    handleMergeDetData(rows) {
      rows.forEach((row) => {
        const rowData = row;
        const detDataList = row?.pcTNeedCollectDetlDTOS || [];
        const needGatherYear = this.needGatherYear || '';
        const needGatherYearArr = needGatherYear.split(',');
        needGatherYearArr.forEach((item) => {
          const needGatherYearValue = item;
          const needYearListFormat = this.needYearListFormat || {};
          const visibleWeightColumn = needYearListFormat[item];
          let totalWeight = 0;
          detDataList.forEach((detItem) => {
            if (needGatherYearValue === `${detItem.needYear}`) {
              totalWeight += Number(detItem.weight || 0);
            }
          });

          rowData[visibleWeightColumn] = totalWeight.toFixed(2);
        });
      });
      return rows;
    },
    // 处理单位切换
    handleUnitParse(rows) {
      const newTableData = [];
      const handleOlDataRow = rows;

      handleOlDataRow.forEach((item) => {
        const obj = item;
        obj.weight1 = obj.weight1 ? (obj.weight1 / this.weightUnit).toFixed(2) : '0.00';
        obj.weight2 = obj.weight2 ? (obj.weight2 / this.weightUnit).toFixed(2) : '0.00';
        obj.weight3 = obj.weight3 ? (obj.weight3 / this.weightUnit).toFixed(2) : '0.00';
        obj.weight4 = obj.weight4 ? (obj.weight4 / this.weightUnit).toFixed(2) : '0.00';
        newTableData.push(obj);
      });
      this.tableData.rows = newTableData;
    },

    // 详细
    onDetail(row) {
      const data = this.handleToViewData(row, 'detail');
      data.needGatherYear = this.needGatherYear;
      data.batchName = this.queryItems.batchName;
      this.$refs.Detail.show(data);
    },

    // 处理传入到下一个页面的数据
    handleToViewData(row, type) {
      const that = this;
      const weightUnitObj = {
        weightUnit: this.weightUnit,
        weightUnitName: this.weightUnitName,
      };
      const selectedRows = _.cloneDeep(row); // 深拷贝
      that.mergeDataList = [];
      // 如果是编辑的话，处理对应的小等级列表数据
      if (type === 'editor') {
        const detlList = selectedRows.pcTNeedCollectDetlDTOS || [];
        this.mergeDataList = detlList;
        for (let index = 0; index < detlList.length; index++) {
          const item = detlList[index];
          this.$apis.apm.demandSubmit
            .queryLevel({
              pageNo: 1,
              pageSize: 100,
              leafTypeCode: item.leafTypeCode,
            })
            .then((res) => {
              this.$set(item, 'leafLevelList', res.rows);
            })
            .then(() => {
              if (index === detlList.length - 1) {
                that.mergeDataList = item;
              }
            });
        }
        selectedRows.pcTNeedCollectDetlDTOS = that.mergeDataList;
      }

      // const { needGatherYear } = this;
      const weightTitleObj = {
        weight1Title: this.weight1Title,
        weight2Title: this.weight2Title,
        weight3Title: this.weight3Title,
        weight4Title: this.weight4Title,
      };
      const { needYearListFormat } = this;

      const data = {
        weightUnitObj,
        businessYear: this.queryItems.businessYear,
        selectedRows,
        weightTitleObj,
        needYearListFormat,
        // needGatherYear: this.needGatherYear,
        type,
      };
      return data;
    },

    // 退回
    onBack() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再退回');
        return;
      }
      const submitData = this.selectedRows.find((item) => item.auditState !== '1');
      console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('存在不是【省公司待确认】的数据，请重新选择！');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认退回选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].needCollectMasId);
          }
          this.$apis.apm.demandConfirm
            .back({ needCollectMasIds: idList })
            .then((res) => {
              console.log(JSON.stringify(res));
              if (res.result) {
                this.loading = false;
                this.$refs.table.pageAction(1);
                this.$message.success('操作成功');
              } else {
                this.loading = false;
                this.$message.error('操作失败');
              }
            })
            .catch((error) => {
              this.loading = false;
              if (error) {
                this.$message.error(error.message || '操作失败');
              } else {
                this.$message.error('操作失败');
              }
            });
        },
      });
    },

    // 同意
    onAgree() {
      if (!this.selectedRows || this.selectedRows.length <= 0) {
        this.$message.error('请选择一条数据再同意');
        return;
      }
      const submitData = this.selectedRows.find((item) => item.auditState !== '1');
      console.log(`--${JSON.stringify(submitData)}`);
      if (submitData) {
        this.$message.error('存在不是【省公司待确认】的数据，请重新选择！');
        return;
      }
      this.$confirm({
        title: '是否执行该操作?',
        content: () => (
          <div style="color:red;">
            是否确认同意选中的数据，确认请点击'确认'按钮，取消点击'取消'按钮。
          </div>
        ),
        onOk: () => {
          this.loading = true;
          const idList = [];
          for (let i = 0; i < this.selectedRows.length; i++) {
            idList.push(this.selectedRows[i].needCollectMasId);
          }
          this.$apis.apm.demandConfirm
            .agree({ needCollectMasIds: idList })
            .then((res) => {
              console.log(JSON.stringify(res));
              if (res.result) {
                this.loading = false;
                this.$refs.table.pageAction(1);
                this.$message.success('操作成功');
              } else {
                this.loading = false;
                this.$message.error('操作失败');
              }
            })
            .catch((error) => {
              this.loading = false;
              if (error) {
                this.$message.error(error.message || '操作失败');
              } else {
                this.$message.error('操作失败');
              }
            });
        },
      });
    },

    // 重置
    onReset() {
      this.queryItems.auditState = '1';
      this.queryItems.batchName = this.defaultBatchName;
      this.queryItems.supplyOrgCode = undefined;
      this.queryItems.demandOrgCid = undefined;
      this.$refs.table.pageAction(1);
    },

    // 合计数据的处理
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field] || 0);
      });
      return count.toFixed(2);
    },
    // 底部合计行
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return '合计';
          }
          if (['weight1', 'weight2', 'weight3', 'weight4'].includes(column.property)) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },

    // 选中所有列表
    selectAllEvent({ checked, records }) {
      console.log(`${checked}-选中所有records包含选中的所以对象----${JSON.stringify(records)}`);
      this.selectedRows = records;
    },

    // 选中列表
    selectRowChange({ checked, records }) {
      console.log(
        `${checked}-选择改变时会把records包含选中的所以对象----${JSON.stringify(records)}`,
      );
      this.selectedRows = records;
    },
  },
};
</script>
