<template>
  <ec-drawer
    title="需求提报确认详细"
    :visible="visible"
    size="large"
    @close="cancel"
    class="ec-draw-form"
  >
    <ec-container height="auto" fixed="top bottom" color-code="007">
      <ec-container-body>
        <ec-panel title="需求主信息" color-code="001">
          <ec-panel-item>
            <ec-form ref="ruleForm1" :model="formData" :rules="rules">
              <a-row type="flex" :gutter="12">
                <a-col :span="8">
                  <a-form-model-item label="供方单位">{{
                    formData.supplyOrgName || '--'
                  }}</a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="采集批次">{{
                    formData.batchName || '--'
                  }}</a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="业务年度">{{
                    formData.busiYear || '--'
                  }}</a-form-model-item>
                </a-col>
                <a-col :span="8">
                  <a-form-model-item label="录入日期">
                    {{ formatDate(formData.createTime) || '--' }}</a-form-model-item
                  >
                </a-col>
              </a-row>
            </ec-form>
          </ec-panel-item>
        </ec-panel>

        <ec-panel>
          <ec-panel-toolbar>
            <ec-panel-item-right>
              <span>计量单位：{{ weightUnitName }}</span>
            </ec-panel-item-right>
          </ec-panel-toolbar>
          <ec-panel-body>
            <ec-table
              border
              keep-source
              ref="table"
              show-footer
              row-select
              :edit-rules="validRules"
              :footer-method="footerMethod"
              :data="tableData"
            >
              <ec-table-col width="70" title="序号" type="seq" align="center" />
              <ec-table-col
                field="leafTypeName"
                title="烟叶类型"
                min-width="180"
                align="left"
                :edit-render="{}"
              >
                <template #edit="{ row }">
                  <a-select
                    v-model="row.leafTypeName"
                    :filter-option="filterOption"
                    @select="handleGetLeafLevel(row)"
                  >
                    <!-- @select="(value, key) => projectSelect(value, key)" -->

                    <a-select-option
                      v-for="item in leafTypeList"
                      :key="item.leafTypeCode"
                      :value="item.leafTypeName"
                      >{{ item.leafTypeName }}</a-select-option
                    >
                  </a-select>
                </template>
              </ec-table-col>

              <ec-table-col
                field="leafLevelName"
                title="小等级"
                min-width="180"
                align="left"
                :edit-render="{}"
              >
                <template #edit="{ row }">
                  <a-select
                    v-model="row.leafLevelName"
                    :filter-option="filterOption"
                    @select="handleLeafLevelCode(row)"
                  >
                    <a-select-option
                      v-for="item in row.leafLevelList"
                      :key="item.leafLevelCode"
                      :value="item.leafLevelName"
                      >{{ item.leafLevelName }}</a-select-option
                    >
                  </a-select>
                </template>
              </ec-table-col>
              <ec-table-col
                field="weight1"
                :title="weight1Title"
                align="right"
                min-width="180"
                :edit-render="{}"
              >
                <template #edit="{ row }">
                  <a-input-number
                    v-model="row.weight1"
                    :min="0"
                    :precision="2"
                    @blur="handleNoValue(row, 'weight1')"
                  ></a-input-number>
                </template>
              </ec-table-col>
              <ec-table-col
                field="weight2"
                :title="weight2Title"
                align="right"
                min-width="180"
                :edit-render="{}"
              >
                <template #edit="{ row }">
                  <a-input-number
                    v-model="row.weight2"
                    :min="0"
                    :precision="2"
                    @blur="handleNoValue(row, 'weight2')"
                  ></a-input-number>
                </template>
              </ec-table-col>
              <ec-table-col
                field="weight3"
                :title="weight3Title"
                align="right"
                min-width="180"
                :edit-render="{}"
              >
                <template #edit="{ row }">
                  <a-input-number
                    v-model="row.weight3"
                    :min="0"
                    :precision="2"
                    @blur="handleNoValue(row, 'weight3')"
                  ></a-input-number>
                </template>
              </ec-table-col>
              <ec-table-col
                field="weight4"
                :title="weight4Title"
                align="right"
                min-width="180"
                :edit-render="{}"
              >
                <template #edit="{ row }">
                  <a-input-number
                    v-model="row.weight4"
                    :min="0"
                    :precision="2"
                    @blur="handleNoValue(row, 'weight4')"
                  ></a-input-number>
                </template>
              </ec-table-col>
            </ec-table>
          </ec-panel-body>
        </ec-panel>
      </ec-container-body>
      <ec-container-footer fixed>
        <a-space>
          <a-button @click="cancel">关闭</a-button>
        </a-space>
      </ec-container-footer>
    </ec-container>
  </ec-drawer>
</template>
<script>
import { OrgPicker, OrgTree, DivisionPicker } from '@euler-material/ecm-ui';
import moment from 'moment';
import _ from 'lodash';

export default {
  name: 'AdminOrgManageEdit',
  components: {
    OrgPicker,
    OrgTree,
    DivisionPicker,
    moment,
  },
  data() {
    const orgNameValid = (rule, value, callback) => {
      if (value.length === 0) {
        return callback(new Error('请选择供方单位'));
      }

      callback();
    };
    return {
      title: '',
      isDisableYear: true,
      isEdit: false,
      rowData: '',
      visible: false,
      checked: false,
      ruleForm: {},
      formData: {},
      yearDemandList: [],
      leafTypeList: [],
      totalLeafLevelList: [],
      orgCodeList: [],
      businessYear: '',
      weightUnitName: '',
      weightUnit: '',
      tableConfig: [],
      weight1Title: '',
      weight2Title: '',
      weight3Title: '',
      weight4Title: '',
      needYearListFormat: {},
      needGatherYear: '',
      weightColunm: ['weight1', 'weight2', 'weight3', 'weight4'],
      tableData: [],
      oldTableData: [],
      mergeTableData: [],
      currentLeafLevelList: [],
      data: {},
      validRules: {
        leafTypeName: [{ required: true, message: '请选择烟叶类型' }],
        leafLevelName: [{ required: true, message: '请选择小等级' }],
      },
      rules: {
        orgCodeList: [{ validator: orgNameValid, required: true, trigger: 'change' }],
      },
    };
  },
  // mounted() {
  //   this.handleTableColumn();
  // },
  methods: {
    moment,
    formatDate(createDate) {
      return moment(createDate).format('YYYY-MM-DD');
    },
    // 下拉框过滤
    filterOption(input, option) {
      return (
        option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
      );
    },
    // 处理输入空数据
    handleNoValue(row, field) {
      const rowObj = row;
      const value = rowObj[field];
      if (!value) {
        rowObj[field] = '0.00';
      }
    },
    // 显示推窗
    async show(data) {
      this.visible = true;
      await this.handleGetLeafTypeList();
      // 初始化formData
      this.weightUnit = data.weightUnitObj.weightUnit;
      this.weightUnitName = data.weightUnitObj.weightUnitName;
      const businessYear = data.businessYear > 0 ? data.businessYear : new Date().getFullYear();
      this.businessYear = businessYear;

      this.needGatherYear = data.needGatherYear || '';
      const needGatherYearArr = this.needGatherYear.split(',');
      this.needGatherYearArr = needGatherYearArr;

      // 如果是编辑
      this.title = '需求提报编辑';
      this.isEdit = true;

      const selectedRows = data?.selectedRows;
      const supplyOrgCode = selectedRows?.supplyOrgCode;
      const orgCodeList = [];
      if (supplyOrgCode) {
        orgCodeList.push(supplyOrgCode);
      }
      const weightTitleObj = data.weightTitleObj || {};
      this.weight1Title = weightTitleObj.weight1Title;
      this.weight2Title = weightTitleObj.weight2Title;
      this.weight3Title = weightTitleObj.weight3Title;
      this.weight4Title = weightTitleObj.weight4Title;
      this.needYearListFormat = data.needYearListFormat;
      this.formData = data?.selectedRows;
      this.formData.orgCodeList = orgCodeList;
      const tableData = data?.selectedRows?.pcTNeedCollectDetlDTOS;
      // 将细表里面的数据，根据小等级进行合并,在根据年度合并
      // this.oldTableData = _.cloneDeep(tableData);
      const mergeTableData = await this.handleMergeDetData(tableData);
      this.mergeTableData = _.cloneDeep(mergeTableData);
      await this.handleUnitParse(mergeTableData);
    },

    // 将细表里面的数据，根据小等级进行合并,在根据年度合并
    async handleMergeDetData(detlList) {
      const mergeLevelArr = [];
      // 根据小等级合并
      detlList.forEach(async (detl) => {
        const detlObj = detl;
        const parent = mergeLevelArr.find(
          (mergeObj) => mergeObj.leafLevelCode === detl.leafLevelCode,
        );
        if (parent) {
          parent.detlList.push(detlObj);
        } else {
          // 烟叶类型名称
          const { leafTypeCode } = detlObj;
          const leafTypeArr = this.leafTypeList.find((item) => item.leafTypeCode === leafTypeCode);
          // 获取烟叶小等级名称，及对应的小等级列表
          const { leafLevelCode } = detlObj;

          const leafLevelArr = this.totalLeafLevelList.find(
            (item) => item.leafLevelCode === leafLevelCode,
          );

          const obj = {
            leafTypeName: leafTypeArr?.leafTypeName,
            leafLevelName: leafLevelArr?.leafLevelName,
            leafLevelCode,
            leafTypeCode,
            leafLevelList: detlObj.leafLevelList,
            detlList: [detlObj],
          };
          mergeLevelArr.push(obj);
        }
      });

      // 先把固定4个需求列表隐藏
      this.weightColunm.forEach((item) =>
        this.$refs.table.hideColumn(this.$refs.table.getColumnByField(item)),
      );

      // 根据小等级合并同一个小等级的需求年度数据
      mergeLevelArr.forEach((row) => {
        const rowData = row;
        const detDataList = row?.detlList || [];
        const needGatherYear = this.needGatherYear || '';
        const needGatherYearArr = needGatherYear.split(',');
        needGatherYearArr.forEach((item) => {
          const needGatherYearValue = item;
          const needYearListFormat = this.needYearListFormat || {};
          const visibleWeightColumn = needYearListFormat[item];
          let totalWeight = 0;
          detDataList.forEach((detItem) => {
            if (needGatherYearValue === `${detItem.needYear}`) {
              totalWeight += Number(detItem.weight || 0);
            }
          });

          rowData[visibleWeightColumn] = totalWeight.toFixed(2);
          // 显示动态需求列
          this.$refs.table.showColumn(this.$refs.table.getColumnByField(visibleWeightColumn));
        });
      });

      return mergeLevelArr;
    },

    // 处理单位切换
    async handleUnitParse(rows) {
      const newTableData = [];
      const handleOlDataRow = rows;
      if (handleOlDataRow && handleOlDataRow.length > 0) {
        handleOlDataRow.forEach((item) => {
          const obj = item;
          obj.weight1 = obj.weight1 ? (Number(obj.weight1) / this.weightUnit).toFixed(2) : '0.00';
          obj.weight2 = obj.weight2 ? (Number(obj.weight2) / this.weightUnit).toFixed(2) : '0.00';
          obj.weight3 = obj.weight3 ? (Number(obj.weight3) / this.weightUnit).toFixed(2) : '0.00';
          obj.weight4 = obj.weight4 ? (Number(obj.weight4) / this.weightUnit).toFixed(2) : '0.00';
          newTableData.push(obj);
        });
        this.tableData = newTableData;
      }
    },
    // 获取烟叶类型数据
    async handleGetLeafTypeList() {
      if (!this.leafTypeList?.length) {
        const res = await this.$apis.bpm.executionEnter.queryAllLeafType({});
        this.leafTypeList = res?.rows || [];
      }
      if (!this.totalLeafLevelList?.length) {
        const data = {
          pageNo: 1,
          pageSize: 150,
          leafTypeCode: '',
        };
        const res = await this.$apis.apm.demandSubmit.queryAllLevel(data);
        this.totalLeafLevelList = res?.rows || [];
      }
    },

    // 处理烟叶小等级的编码
    handleLeafLevelCode(row) {
      const rowObj = row;
      const leafLevelList = row.leafLevelList || [];
      leafLevelList.forEach((item) => {
        const leafLevel = item;
        if (leafLevel.leafLevelName === row.leafLevelName) {
          rowObj.leafLevelCode = leafLevel.leafLevelCode;
        }
      });
    },

    // 点击烟叶类型的时候处理对应烟叶小等级，以及将之前的小等级重置
    async handleGetLeafLevel(row) {
      const rowObj = row;
      const { leafTypeList } = this;
      leafTypeList.forEach((item) => {
        const obj = item;
        if (obj.leafTypeName === row.leafTypeName) {
          rowObj.leafTypeCode = obj.leafTypeCode;
        }
      });
      const data = {
        pageNo: 1,
        pageSize: 100,
        leafTypeCode: row.leafTypeCode || '',
      };
      const res = await this.$apis.apm.demandSubmit.queryLevel(data);
      rowObj.leafLevelCode = '';
      rowObj.leafLevelName = '';
      rowObj.leafLevelList = res?.rows;
    },

    // 新增行
    async addRow() {
      const record = {
        leafTypeCode: '',
        leafTypeName: '',
        leafLevelCode: '',
        leafLevelName: '',
        leafLevelList: [],
        weight1: '0.00',
        weight2: '0.00',
        weight3: '0.00',
        weight4: '0.00',
        dataState: '1',
        busiYear: this.businessYear,
      };

      this.tableData.push(record);
    },
    // 删除行
    onDel(index) {
      this.tableData.splice(index, 1);
    },
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 0) {
            return `合计`;
          }
          if (['weight1', 'weight2', 'weight3', 'weight4'].includes(column.property)) {
            return this.sumNum(data, column.property);
          }
          return null;
        }),
      ];
    },
    sumNum(list, field) {
      let count = 0;
      list.forEach((item) => {
        count += Number(item[field] ? item[field] : 0);
      });
      return count.toFixed(2);
    },
    // 处理保存之前饿数据（单位转成计量单位、一条数据根据需求年度拆分成多条细表数据）
    handleBeforeSaveData() {
      const { formData } = this;
      // 拷贝一份处理数据，避免保存失败，界面显示的还是要处理前的数据
      const tabelData = _.cloneDeep(this.tableData);
      // 先判断是否存在相同的小等级，如果存在的话，提示不能存在相同的
      const leafLevelArr = [];
      this.isHasLeafLevel = false;
      tabelData.forEach((item) => {
        const obj = item;
        const { leafLevelCode } = obj;
        const repeat = leafLevelArr.find((level) => level === leafLevelCode);
        if (repeat && repeat.length > 0) {
          this.isHasLeafLevel = true;
          this.$message.error(`存在重复的小等级[${obj.leafLevelName}]`);
        } else {
          leafLevelArr.push(leafLevelCode);
        }
      });
      if (this.isHasLeafLevel) {
        return;
      }
      const { orgCodeList } = formData;
      const orgCode = orgCodeList.length > 0 ? orgCodeList[0] : '';

      const saveData = this.formData;
      saveData.supplyOrgCode = orgCode;

      const pcTNeedCollectDetlDTOS = [];

      // 组合细表数据
      tabelData.forEach((tableItem) => {
        const tableObj = tableItem;
        const needGatherYearArr = this.needGatherYearArr || [];
        if (needGatherYearArr && needGatherYearArr.length > 0) {
          needGatherYearArr.forEach((item) => {
            const needGatherYearItem = item;
            const needYearListFormat = this.needYearListFormat || {};
            const visibleWeightColumn = needYearListFormat[needGatherYearItem];
            const tableWeight = tableItem[visibleWeightColumn] || 0;
            const weight = (Number(tableWeight) * this.weightUnit).toFixed(2);
            const delObj = {
              weight,
              needYear: needGatherYearItem,
              leafTypeCode: tableObj.leafTypeCode,
              leafLevelCode: tableObj.leafLevelCode,
              busiYear: this.businessYear,
              dataState: '1',
            };
            pcTNeedCollectDetlDTOS.push(delObj);
          });
        }
      });
      saveData.pcTNeedCollectDetlDTOS = pcTNeedCollectDetlDTOS;

      return saveData;
    },

    // 保存
    async onSave(type) {
      const $table = this.$refs.table;
      const errMap = await $table.validate(true).catch((err) => err);
      if (errMap) {
        this.$message.error('校验不通过！');
      } else {
        this.$refs.modelRef.validate(async (valid) => {
          if (!valid) return;
          this.saving = true;

          // 在保存之前将表格里面的调整的数据，进行转化成基础单位（数据库保存的是基础单位）
          const saveData = this.handleBeforeSaveData();
          if (!saveData) {
            return;
          }
          console.log(saveData);

          this.$message.success('操作成功');
          if (type === 'saveAndAdd') {
            this.formData.orgCodeList = [];
            this.tableData = [];
          } else {
            this.cancel();
          }
        });
      }
    },
    // 取消
    cancel() {
      this.visible = false;
    },
  },
};
</script>
